import React from "react";
import ReactDOM from "react-dom/client";
import "./index.css";
import App from "./App";
import NavBar from "./layout/Navbar";
import NewAnimeForm from "./layout/newAnimeForm";
import reportWebVitals from "./reportWebVitals";
import { redirect, RouterProvider } from "react-router-dom";
import { createBrowserRouter } from "react-router-dom";
import "bootstrap/dist/css/bootstrap.css";
import ErrorPage from "./layout/Error";
import { json } from "react-router-dom";
import AnimeDetail from "./layout/AnimeDetail";

async function getAllList() {
  const res = await fetch("/allAnime");
  const data = await res.json();
  return data;
}

async function getOneAnime({ params }) {
  const id = params.id;
  let fdata = {};
  await fetch("/animeDetail/" + id)
    .then((response) => response.json())
    .then((data) => {
      // console.log(data.anime);
      if (data.status >= 400) {
        throw new Response(data.message, { status: data.status });
      }
      fdata = data;
    })
    .catch((error) => console.log(error));
  return fdata;
}

const router = createBrowserRouter([
  {
    path: "/",
    element: <NavBar />,
    children: [
      {
        path: "/error",
        element: <ErrorPage />,
      },
      {
        path: "/",
        element: <App />,
        loader: getAllList,
        errorElement: <ErrorPage />,
      },
      {
        path: "/newAnime",
        element: <NewAnimeForm />,
        errorElement: <ErrorPage />,
      },
      {
        path: "/animeDetail/:id",
        loader: getOneAnime,
        element: <AnimeDetail />,
      },
    ],
  },
]);
const root = ReactDOM.createRoot(document.getElementById("root"));
root.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
