import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";
import { Link, useLoaderData } from "react-router-dom";
import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";
import { useNavigate } from "react-router-dom";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import React, { useState, useEffect } from "react";

function AnimeDetail() {
  const navigate = useNavigate();
  const oneAnime = useLoaderData().anime;
  const [loading, setloading] = useState(false);
  const [reveiws, setReviews] = useState(oneAnime.review);

  async function handleDelete(id) {
    const port = "/review/" + oneAnime._id + "/" + id;
    setloading(true);
    await fetch(port, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        console.log(data);
        setReviews(data.oneAnime.review);
      })
      .catch((error) => {
        console.error("There was a problem with your fetch operation:", error);
      });
    // setReviews((pre) => {
    //   const now = pre.splice(indexToRemove, 1);
    //   // console.log(pre);
    //   console.log(now);
    //   return pre;
    // });
    setloading(false);
  }
  function handleSubmit(event) {
    event.preventDefault();
    let formData = new FormData(event.target);
    // for (var pair of formData.entries()) {
    //   console.log(pair[0] + ", " + pair[1]);
    // }
    setloading(true);

    // console.log(event.target);
    // console.log(oneAnime._id);
    fetch("/newReview/" + oneAnime._id, {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        // console.log(data.oneAnime.review);

        if (data.statusCode >= 400) {
          return (
            // <FlashMessage duration={5000}>
            //   <strong>Important:</strong> This is a flash message!
            // </FlashMessage>
            navigate("/newAnime")
          );
        }
        setReviews(data.oneAnime.review);
        return navigate("/animeDetail/" + oneAnime._id);
      })
      .catch((error) => {
        console.error(error);
      });
    setloading(false);
  }
  return (
    <>
      {loading ? (
        <p>it is loading</p>
      ) : (
        <Container>
          <Row className="mb-3">
            <Col sm={8}>
              <Card style={{ width: "18rem" }}>
                <Card.Img variant="top" src={oneAnime.image.url} />
                <Card.Body>
                  <Card.Title>{oneAnime.animeName}</Card.Title>
                  <Card.Text>rate: {oneAnime.rate}</Card.Text>
                </Card.Body>
                <ListGroup className="list-group-flush">
                  <ListGroup.Item>producer:{oneAnime.producer}</ListGroup.Item>
                </ListGroup>
              </Card>
            </Col>
            <Col sm={4}>
              <Form onSubmit={handleSubmit}>
                <Form.Group
                  className="mb-3"
                  controlId="exampleForm.ControlTextarea1"
                >
                  <Form.Label>comment</Form.Label>
                  <Form.Control as="textarea" rows={3} name="comment" />
                </Form.Group>
                <Form.Label>rate</Form.Label>
                <Form.Range name="score" />

                <Button variant="primary" type="submit">
                  Submit
                </Button>
              </Form>
            </Col>
          </Row>
          <Row>
            {reveiws.map((item) => (
              <Card style={{ width: "18rem" }} className="mb-3 me-3">
                <Card.Body>
                  <Card.Text>{item.body}</Card.Text>
                  <Card.Text>Rate:{item.rating}</Card.Text>
                </Card.Body>
                <Button variant="danger" onClick={() => handleDelete(item._id)}>
                  Delete
                </Button>
              </Card>

              // <li key={item.body}>
              //   {item.rating}:{item.body}
              // </li>
            ))}
          </Row>
        </Container>
      )}
    </>
  );
}

export default AnimeDetail;
