import SingleCard from "./SingleCard";
import ListGroup from "react-bootstrap/ListGroup";
import Container from "react-bootstrap/Container";
import { Link, useLoaderData } from "react-router-dom";
import Button from "react-bootstrap/Button";
import React, { useState, useEffect } from "react";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";

function AnimeList() {
  const AnimeList = useLoaderData().AllList;
  const [loading, setloading] = useState(false);
  const [Allanime, setAllanime] = useState(AnimeList);

  async function deleteClicked(oneAnime) {
    setloading(true);
    await fetch(oneAnime._id, {
      method: "DELETE",
      headers: {
        "Content-Type": "application/json",
      },
    })
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        // console.log(data.AllList);
        setAllanime(data.AllList);
      })
      .catch((error) => {
        console.error("There was a problem with your fetch operation:", error);
      });

    setloading(false);
  }

  // const ListAnime = (List) => <li class="list-group-item">{oneCard.url}</li>;
  function ListAnime() {
    return Allanime.map((oneCard) => (
      <Col md={{ span: 4, offset: 2 }}>
        <SingleCard
          oneAnime={oneCard}
          deleteClicked={deleteClicked}
          loading={loading}
        ></SingleCard>
      </Col>
    ));
  }
  return (
    <>
      {loading ? (
        <p>it is loading</p>
      ) : (
        <Container>
          <Button variant="outline-primary">
            <Link to="/newAnime">New</Link>
          </Button>{" "}
          {/* <ListGroup variant="flush" className="align"> */}
          <Row>{ListAnime()}</Row>
          {/* </ListGroup> */}
        </Container>
      )}
    </>
  );
}

export default AnimeList;
