import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Row from "react-bootstrap/Row";
import { useNavigate } from "react-router-dom";

import React from "react";
import FlashMessage from "react-flash-message";
import Alert from "react-bootstrap/Alert";
import { useReducer } from "react";

function reducer(state, action) {
  switch (action.type) {
    case "error":
      return { error: action.error, loading: false };
    case "beforeFetch":
      return { error: false, loading: true };
    case "fetchSuccess":
      return { error: false, loading: false };
    default:
      throw new Error();
  }
}

function NewAnimeForm() {
  const [fetchState, dispatch] = useReducer(reducer, {
    loading: false,
    error: false,
  });

  const navigate = useNavigate();

  const [file, setFile] = React.useState(null);
  // const [error, setError] = React.useState(false);
  // const [loading, setloading] = React.useState(false);

  function handleFileChange(event) {
    let selectedFile = event.target.files[0];
    setFile(selectedFile);
  }

  function handleSubmit(event) {
    event.preventDefault();

    // for (let i = 0; i <= 4; i++) {
    //   console.log(event.target[i].value);
    // }

    let formData = new FormData(event.target);
    for (var pair of formData.entries()) {
      console.log(pair[0] + ", " + pair[1]);
    }
    dispatch({ type: "beforeFetch" });
    fetch("/newAnime", {
      method: "POST",
      body: formData,
    })
      .then((response) => response.json())
      .then((data) => {
        if (data.statusCode >= 400) {
          console.log("error");
          dispatch({ type: "error", error: data });
          return (
            // <FlashMessage duration={5000}>
            //   <strong>Important:</strong> This is a flash message!
            // </FlashMessage>
            navigate("/newAnime")
          );
        }
        dispatch({ type: "fetchSuccess" });
        return navigate("/");
      })
      .catch((error) => {
        console.error(error);
      });
  }

  return (
    <>
      {fetchState.error && (
        <FlashMessage duration={5000}>
          <Alert variant="danger">{fetchState.error.message}</Alert>
          <strong>{fetchState.error.message}:</strong>
        </FlashMessage>
      )}

      {/* <form onSubmit={handleSubmit}>
        // <label htmlFor="file">Select a file:</label>
        //{" "}
        <input type="file" id="file" name="file" onChange={handleFileChange} />
        // <button type="submit">Upload</button>
        //{" "}
      </form> */}
      {fetchState.loading ? (
        <p>it is loading</p>
      ) : (
        <Form onSubmit={handleSubmit} method="Post">
          <Row className="mb-3">
            <Form.Group as={Col} controlId="formGridEmail">
              <Form.Label>Email</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter email"
                name="email"
              />
            </Form.Group>
          </Row>

          <Form.Group controlId="formFile" className="mb-3">
            <Form.Label>import an image</Form.Label>
            <Form.Control
              onChange={handleFileChange}
              type="file"
              name="image"
            />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAnimeName">
            <Form.Label>Anime name</Form.Label>
            <Form.Control placeholder="your favourite anime" name="animeName" />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAnimeProducer">
            <Form.Label>producer</Form.Label>
            <Form.Control
              placeholder="the producer of the anime"
              name="producer"
            />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAnimeProducer">
            <Form.Label>Range</Form.Label>
            <Form.Range name="rate" />
          </Form.Group>

          <Button variant="primary" type="submit">
            Submit
          </Button>
        </Form>
      )}
    </>
  );
}

export default NewAnimeForm;
